<template>
  <div class="user" v-if="user">
    <div class="user__left">
      <img :src="user.avatar_url" alt="" />
      <a :href="user.html_url">VISIT</a>
    </div>

    <div class="user__right">
      <p>{{ user.login }}</p>
      <p>
        Repositories:<span>{{ user.public_repos }}</span>
      </p>
      <p>
        Created:<span>{{
          new Date(user.created_at).toLocaleDateString()
        }}</span>
      </p>
      <p>
        Followers:<span>{{ user.followers }}</span>
      </p>
      <p>
        Followings:<span>{{ user.following }}</span>
      </p>
    </div>
  </div>
  <div class="sort">
    <h2 class="sort__title">Sorting</h2>
    <div class="sort__box">
      <button class="sort__btn" 
      v-for="(btn, i) in btns" 
      :key="i"
      :class="{active: isActive == i}"
      @click="btnActive(i, btn.type)"
      >
        {{ btn.name }}
      </button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
    data(){
        return {
            btns:[{ name:"ИМЯ", type:'name'}
                , { name:"ЗВЕЗДЫ", type:'stargazers_count'}
                , { name:"ДАТА", type:'created_at'}],
            isActive:0,
        }
    },
  computed: {
    ...mapState(["user"]),
  },
  methods:{
    ...mapMutations(['sortRepos']),
    btnActive(i, type){
        this.isActive = i
        this.sortRepos(type)
    }
  }
};
</script>

<style lang="scss" scoped>
.sort {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  &__title {
    color: #3f3f3f;
    font-size: 18px;
    font-weight: 700;
    line-height: 120%;
    text-transform: capitalize;
  }
  &__box {
    max-width: 348px;
    width: 100%;
    border-radius: 4px;
    background: #e0e0e0;
    padding: 2px;
    display: flex;
    gap: 2px;
  }
  &__btn {
    border: none;
    outline: none;
    width: 100%;
    border-radius: 4px;
    background: transparent;
    color: #3f3f3f;
    font-size: 16px;
    font-weight: 700;
    line-height: 120%;
    text-transform: capitalize;
    &.active{
        background: #fff;
    }
  }
}
</style>