import axios from 'axios';
import {
    createStore
} from 'vuex'

const store = createStore({
    state: {
        user: null,
        repos: null,
        error: null,
        url: 'https://api.github.com/users/',
        currentSort: ' name'
    },
    mutations: {
        sortRepos(state, payload) {
            state.currentSort = payload
        },
        getUserInfo(state, users) {
            state.user = users
            state.error = null
        },
        userClear(state) {
            state.user = null
            state.repos = null
        },
        getError(state, error) {
            if (error.response.status == 403) {
                state.error = 'U were banned for a short time!'
            } else {
                state.error = 'Something went wrong!'
            }
        },
        reposInfo(state, rep) {
            state.repos = rep
        }


    },
    actions: {
        async getUserInfo({
            commit,
            state
        }, search) {
            try {
                const res = await axios.get(`${state.url}${search}`)
                const repos = await axios.get(`${state.url}${search}/repos`)
                commit('getUserInfo', res.data)
                commit('reposInfo', repos.data)
            } catch (error) {
                commit('getError', error)
                console.log('Error while getting info!:(');
            }
        }
    },
    getters: {
        getReposSort(state) {
            if (state.repos != null) {
                return state.repos.sort((prev, next)=> {
                    if (prev[state.currentSort] > next[state.currentSort]) {
                        return 1
                    }
                    if (prev[state.currentSort] <= next[state.currentSort]) {
                        return -1
                    }
                })
                
                
            }
        }
    }
})

export default store