<template>
  <div class="search">
    <div class="search__item">
      <input type="text"
       placeholder="Введите имя пользователя"
       v-model="search"
       @keydown.enter="getUser"
       />
      <span @click="clear">❌</span>
    </div>
    <button @click="getUser">НАЙТИ</button>
  </div>
</template>

<script>
import {mapState, mapActions, mapMutations} from 'vuex'
export default {
  data(){
    return{
      search:'',
    }
  },
  methods:{
    ...mapActions(['getUserInfo']),
    ...mapMutations(['userClear']),
    getUser(){
      this.getUserInfo(this.search)
    },
    clear(){
      this.search = ''
      this.userClear()
    }
  }
};
</script>

